.form-wrapper {
    border: 1px solid #92AC20;
    background-color: #ceae7514;
    padding: 20px 40px;
    border-radius: 10px;
    width: 100%;
    max-width: 900px;

    @media (max-width: 700px) {
        & {
            padding: 10px;
        }
    }

    .form-inputs {
        width: 100%;
        max-width: 1500px;
        min-height: 200px;
        margin: auto;
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 0 30px;

        @media (max-width: 480px) {
            & {
                padding: 10px;
            }
        }

        .form-fieldsets {
            display: flex;
            flex-direction: column;
            width: 100%;
            justify-content: space-between;
            align-items: center;

            @media (max-width: 990px) {
                flex-direction: column;
            }
        }

        .form-fieldset {
            width: 100%;
            margin: 10px 0;
            padding: 20px;
            border-radius: 10px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-around;
            background-color: beige;


            legend {
                color: #000;
                font-weight: 600;
                font-size: 1rem;
            }

            span {
                color: red;
                font-size: 0.7rem;
                font-weight: bold;
                margin-bottom: 10px;
            }
        }

        label {
            color: #596E0D;
            font-weight: 500;
            font-size: 0.9rem;
            width: 100%;
        }

        input,
        select {
            font-size: 1rem;
            padding: 5px;
            margin-top: 5px;
            width: 100%;
            color: #000;
            border: none;
            background-color: #fff;
            box-sizing: border-box;

        }

        input:focus {
            outline: none;
            border-color: #596E0D;
            box-shadow: 0 0 10px rgba(89, 110, 13, 0.6);
        }

        select option {
            background: #fff;
            color: #596E0D;
        }

        .form-button {
            margin-top: 20px;
            padding: 1rem 2rem;
            background-color: #92AC20;
            border-radius: 5px;
            color: black;
            border: none;
            font-weight: 600;
            cursor: pointer;
            font-family: 'Montserrat', sans-serif;
            letter-spacing: .2rem;
            font-size: 1rem;
        }

        .form-button:hover {
            background: white;
            color: black;
            border: 1px solid #92AC20;
        }
    }
}