.hero-not-found {
    background-image: url('../../assets/image/lost.jpg');
    background-position: 60% 80%;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 90vh;
    position: relative;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

}

.hero-not-found-content {
    position: absolute;
    left: 40px;
    top: 200px;
    width: 500px;
    background: rgba(255, 255, 255, 0.7);
    padding: 2rem;
    margin: 0 auto;
    border-radius: 15px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
}

.hero-not-found-content .title {
    font-weight: bold;
    font-size: 1.8rem;
    margin: 0;
}

.hero-not-found-content .text {
    margin-top: 20px;
    margin-bottom: 0;
    font-size: 1.2rem;
    font-weight: 500;
}

.hero-not-found-link {
    text-decoration: none;
    font-weight: bold;
    color: #596E0F;
    background-color: transparent;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
    margin-top: 30px;
    text-transform: uppercase;

    &:hover {
        text-decoration: underline;
    }
}

.sr-only {
    border: 0 !important;
    clip: rect(1px, 1px, 1px, 1px) !important;
    /* 1 */
    -webkit-clip-path: inset(50%) !important;
    clip-path: inset(50%) !important;
    /* 2 */
    height: 1px !important;
    margin: -1px !important;
    overflow: hidden !important;
    padding: 0 !important;
    position: absolute !important;
    width: 1px !important;
    white-space: nowrap !important;
    /* 3 */
}





