.header {
    box-shadow: 1px 8px 12px -2px rgba(201, 201, 201, 0.54);
    min-height: 10vh;
    background-color: white;
    z-index: 10;
    position: relative;

    .header-container {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .no-decoration {
            text-decoration: none;
            color: #000;
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: 700;
        }

        a {
            display: flex;
            align-items: center;
            color: #596E0D;
            font-weight: 600;
        }

        img {
            width: 50px;
            height: 50px;
        }

        .hearder-text {
            margin: 0 10px;
            font-size: 1.2rem;
        }

        .header-tm {
            font-size: 1.4rem;
            color: #000;
            font-weight: 500;
        }

        .header-links {
            display: flex;
            width: 280px;
            justify-content: space-between;
            align-items: center;
        }

        .bigger-police {
            font-size: 1.5rem;
        }
    }

    @media (max-width: 768px) {
        .header-container {
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .header-tm {
                margin: 10px 0;
            }
        }

        .no-decoration {
            display: flex;
            flex-direction: column;
            // margin-bottom: 20px;
        }
    }
}